
import XHeader from 'vux/src/components/x-header/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'


export default {
data () {
return {
form: {},
img1: require('../../assets/images/低产店.png'),
img2: require('../../assets/images/中产店.png'),
img3: require('../../assets/images/高产店.png'),
img4: require('../../assets/images/百万店.png')
}
},
methods: {
_getDetail () {
this.$tupHttp(this, 'manage-psi/targetEstimate/permission-all/getTargetEstimateResult', 'POST').then(res => {
this.form = res.data.model
})
}
},
created () {
this._getDetail()
},
components: {
XHeader,
XTable
}
}



